@font-face {
  font-family: 'Arkiv';
  src: url('fonts/Arkiv-Regular.eot');
  src: url('fonts/Arkiv-Regular.eot?') format('embedded-opentype'), url('fonts/Arkiv-Regular.woff') format('woff'), url('fonts/Arkiv-Regular.woff2') format('woff2');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'Arkiv';
  src: url('fonts/Arkiv-Light.eot');
  src: url('fonts/Arkiv-Light.eot?') format('embedded-opentype'), url('fonts/Arkiv-Light.woff') format('woff'), url('fonts/Arkiv-Light.woff2') format('woff2');
  font-weight: 100;
  font-style: normal;
}

@font-face {
  font-family: 'Arkiv';
  src: url('fonts/Arkiv-Bold.eot');
  src: url('fonts/Arkiv-Bold.eot?') format('embedded-opentype'), url('fonts/Arkiv-Bold.woff') format('woff'), url('fonts/Arkiv-Bold.woff2') format('woff2');
  font-weight: bold;
  font-style: normal;
}

::selection {
  background: #DDB9EB;
}

::-moz-selection {
  background: #DDB9EB;
}

body {
  font-family: 'Arkiv';
  color: #4d4d4d;
  position: relative;
}

h1 {
  margin: 0;
  text-align: center;
  font-size: 2.5em;
  text-transform: uppercase;
  color: white;
}

h2 {
  text-align: center;
  font-size: 2em;
  width: fit-content;
  margin: 0.5em auto 0.2em auto;
  text-transform: uppercase;
}

h3 {
  margin: 1em 0 0 1em;
  font-size: 1.4em;
  width: fit-content;
}

.full-width {
  width: 80% !important;
}

.negative-margin-top {
  margin-top: -1.5em !important;
}

.portfolio-title-container__text-wrapper {
  display: flex;
  width: 100%;
  flex-direction: column;
  height: fit-content;
  position: absolute;
  top: 25%;
}

.name-box {
  width: fit-content;
}

.border-box {
  width: 25%;
  border-bottom: 0.4em solid #9b59b6;
}

p {
  text-align: center;
}

a, a:visited {
  color: #9b59b6;
  text-decoration: none;
}

.background {
  display: flex;
  position: absolute;
  top: 0;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.7);
}

.window {
  z-index: 100;
  width: 50em;
  height: fit-content;
  background-color: white;
  border-radius: 0.5em;
  margin: 5em auto;
  position: relative;
  display: flex;
  flex-direction: column;
}

.close-icon {
  margin: 1em;
  position: absolute;
  right: 0;
  z-index: 999;
  width: 2em;
}

.close-icon:hover {
  cursor: pointer;
}

.header {
  background-color: #2C3E50;
  display: flex;
  flex-direction: row;
}

.header-wrapper {
  width: 75%;
  margin: auto;
  position: relative;
  display: flex;
  flex-direction: row;
}

.info-container {
  margin: 1em 0;
  width: fit-content;
}

.description {
  width: 20em;
  right: 0;
  top: 0;
  height: 12em;
  position: absolute;
  display: flex;
  flex-direction: column;
  background-color: #9b59b6;
}

.description::after {
  position: absolute;
  bottom: -21px;
  right: 0px;
  content: '';
  border-left: 320px solid transparent;
  border-top: 21px solid #9b59b6;
}

.description h4 {
  margin: 1em auto;
  color: white;
  text-align: center;
  text-transform: uppercase;
  font-size: 1.5em;
}

.description p {
  color: white;
  width: 85%;
  margin: 0 auto;
}

.navigation {
  margin: auto 1em auto auto;
}

.navigation a {
  color: white;
  font-size: 1.2em;
  font-weight: bold;
  margin: 0.5em;
  width: fit-content;
  border-bottom: 0.2em solid #2C3E50;
}

.navigation a:hover {
  cursor: pointer;
  transition: 0.2s ease;
}

.contact {
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin: 1em auto;
}

.contact-item {
  margin: 0 1em;
  cursor: pointer;
}

.contact-item img {
  width: 1.2em;
  margin: 0 0.2em;
  vertical-align: middle;
}

.contact-item a {
  text-decoration: none;
  color: #4d4d4d;
  margin: 0;
  display: inline-block;
  vertical-align: middle;
}

.projects {
  display: flex;
  flex-direction: column;
  margin: 4em auto 0 auto;
  width: 75%;
}

.projects h2 {
  font-size: 1.6em;
}

.portfolio-items {
  display: flex;
  flex-direction: row;
  margin: 2em auto 0 auto;
}

.portfolio-item {
  width: 20em;
  position: relative;
  margin: 0 0.5em;
  opacity: 0.95;
  display: flex;
  flex-direction: column;
}

.portfolio-item:hover {
  cursor: pointer;
  transition: 0.3s ease-in-out;
  transform: scale(1.05);
}

.portfolio-item:first-child {
  margin-left: 0
}

.portfolio-item:last-child {
  margin-right: 0
}

.portfolio-item img {
  width: auto;
  height: 20em;
  margin: 0 auto;
}

.portfolio-item__description {
  margin: 0 auto;
}

.portfolio-item__description h3 {
  margin: .5em auto;
  text-align: center;
  text-transform: uppercase;
  font-size: 1.2em;
}

.portfolio-item__description p {
  color: rgba(155, 89, 182, .6);
  text-align: center;
  text-transform: uppercase;
  margin: 0 auto 1em auto;
  font-size: 0.95em;
}

/*.portfolio-item img:hover {
  filter: blur(8px);
}*/

.portfolio-title-container {
  opacity: 0;
  transition: 0.2s ease-in-out;
  width: 100%;
  height: 100%;
  top: 0;
  position: absolute;
  background-color: rgba(0, 0, 0, 0.7);
}

.portfolio-title-container:hover {
  opacity: 1;
}

.portfolio-title-container h3, .portfolio-title-container p {
  padding: 0.5em;
  width: 100%;
  margin: auto;
  padding: 0;
}

.portfolio-title-container h3 {
  text-align: center;
  font-weight: bold;
  color: white;
  font-size: 1.4em;
  margin-bottom: 0;
}

.portfolio-title-container p {
  text-align: center;
  color: white;
  margin-top: 0.5em;
  font-size: 0.9em;
  text-transform: uppercase;
}

.carousel {
  width: 100%;
}

.carousel__media-container {
  display: flex;
}

.carousel img, .carousel video {
  margin: 0 auto;
  height: 25em;
  width: 100%;
}

.portfolio-item-subtitle {
  color: #7F7F7F;
  margin: 0 auto;
}

.portfolio-item-row {
  display: flex;
  flex-direction: row;
  margin: 1em 0;
}

.video-row {
  flex-direction: column;
}

.video-row .text-container {
  width: 75%;
  margin: auto;
}

.video-row .text-container p, .video-row .text-container h3 {
  margin: 16px auto;
  text-align: center;
}

.video-row .video-container {
  width: 100%;
}

.video-container {
  display: flex;
}

.video-container video {
  height: 20em;
  margin: 0 auto 3em auto;
}

ul {
  display: flex;
  flex-direction: column;
}

li {
  font-weight: lighter;
  font-size: 17px;
  text-align: left;
}

.nav-items {
  height: 2em;
  margin: auto;
  display: flex;
  justify-content: center;
}

.nav-items img {
  width: 0.8em;
  margin: 0 0.2em;
  cursor: pointer;
  height: 100%;
}

.img-container {
  position: relative;
  width: 40%;
  height: fit-content;
  display: flex;
  margin: auto;
}

.img-container img {
  margin: auto;
  z-index: 999;
  max-width: 100%;
  height: 22em;
}

.img-container--square-img img {
  width: 95%;
  height: 95%;
  margin-top: 2em;
}

.square-img--left {
  margin-left: 1em;
}

.background-border {
  background: #9b59b6;
  height: 10em;
  width: 16em;
  margin: 7em 0;
  position: absolute;
  z-index: -1;
}

.background-border--forza {
  background: #00d400;
}

.background-border--master-thesis {
  background: #2891c8;
}

.text-container {
  width: 60%;
  margin: auto 1.5em;
}

.text-container h3 {
  background: #2C3E50;
  color: white;
  text-align: center;
  margin-left: 0;
  padding: 0.2em;
}

.text-container p {
  font-size: 1.1em;
  font-weight: lighter;
  margin: 0.5em 0;
  text-align: justify;
}

.text-container--right {
  display: flex;
  flex-direction: column;
}

.text-container--right h3 {
  align-self: flex-end;
}

.muffin-team {
  flex-direction: column;
}

.muffin-team__row {
  display: flex;
  justify-content: space-evenly;
}

.muffin-team__row img {
  height: 5em;
}

.about {
  width: 40em;
  margin: auto;
}

.about p {
  font-size: 1.2em;
  text-align: justify;
  font-weight: lighter;
}

@media screen and (max-width: 1150px) {
  .portfolio-item img {
    height: 15em;
  }
  .portfolio-item__description {
    width: 15em;
  }
}

@media screen and (max-width: 1150px) and (min-width: 851px) {
  .portfolio-items {
    width: 85%;
  }
}

@media screen and (max-width: 1100px) {
  .projects, .header-wrapper {
    width: 85%;
  }
}

@media screen and (max-width: 850px) {
  html, body {
    min-height: 80em;
  }
  .window {
    width: 85%;
  }
  .header {
    flex-direction: column;
  }
  .header-wrapper {
    flex-direction: column;
    margin: auto;
  }
  .navigation {
    margin: 0 auto 2em auto;
  }
  .description {
    top: 10px;
    margin-right: 10%;
    position: relative;
    margin: auto;
  }
  .name-box {
    margin: auto;
  }
  .info-container {
    margin: 1em auto;
  }
  .projects {
    flex-direction: column;
    margin-top: 0;
    width: 90%;
  }
  .projects h2 {
    margin: 2em auto 0.5em auto;
  }
  .portfolio-items {
    flex-direction: column;
  }
  .portfolio-item:first-child {
    margin-top: 0.5em !important;
  }
  .portfolio-item {
    margin: 1em auto 0.5em auto !important;
  }
  .text-container {
    width: 50%;
  }
  .video-container video {
    height: 17em;
  }
  .about {
    width: 75%;
  }
}

@media screen and (max-width: 650px) {
  .window {
    width: 93%;
  }
  .portfolio-item-view h2 {
    width: 95%;
    margin: 1em auto 0 auto;
  }
  .portfolio-item-row {
    flex-direction: column;
  }
  .negative-margin-top {
    margin-top: 1em !important;
  }
  .portfolio-item-row--reverse {
    flex-direction: column-reverse;
  }
  .text-container, .video-row .text-container, .portfolio-item-subtitle {
    margin: auto;
    width: 90%;
  }
  .portfolio-item-subtitle {
    margin: 0.5em auto;
  }
  .img-container {
    width: 100%;
  }
  .img-container img {
    margin: auto;
  }
  .img-container--square-img img {
    width: 90%;
    height: 90%;
    margin-top: 1em;
  }
  .square-img--left {
    margin-left: auto;
  }
  .background-border {
    width: 16em;
  }
  .close-icon {
    margin: 0.5em;
    width: 1.5em;
  }
  .carousel img, .carousel video {
    height: 20em;
  }
  .border-box {
    margin: auto;
  }
  .video-container video {
    height: 9.5em;
    margin-bottom: 0;
  }
  .about {
    width: 85%;
  }
  .about p {
    font-size: 1.1em;
  }
  .muffin-team {
    margin: 1em 0;
  }
}